.App {
  text-align: center;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.movie-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
}
.Movie-wrapper{
  width: 500px;
  background-color: antiquewhite;
  color: black;
  
}
.Movie-search{
  height: 40px;
  font-size: 20px;
  margin-bottom: 30px;
  padding: 15px;
  font-weight: bold;
  border-radius: 15px;
  width: 475px;
}
.Movie-title{
  height :55px;
  margin-bottom: 46px;
  font-weight: bold;

}

.Movie-date{
  font-style: italics;
}

.Movie-rating{
  color: green;
  margin-bottom: 24px;
  font-weight: bold;
  font-size: 1.2em;
  /* text-decoration: underline; */
}
p{
  color: rgb(190, 190, 190);
  font-size: 30px;
  
  margin-bottom: 60px;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
h1{
  font-size: 70px;
}


@media (max-width: 500px ) { 
  .Movie-search{
   width: 50%;
   font-size: 14px;

  }
  .Movie-title{
    margin-bottom: -10px;
    margin-top: 10px;
  }
  h1{
    font-size: 25px;
    margin-bottom: 30px;
  }

  p{
    font-size: 15px;
    margin-bottom: 30px;
  }
  .Movie-wrapper{
    width: 80%;
    

  }
  .Movie-image{
    /* box-sizing: border; */
    width: 100%;
  }
}

